const rating = process.client ? require('vue-rate-it') : {}
export default {
  components: {
    StarRating: rating.StarRating
  },

  watchQuery: ['title'],

  async asyncData ({ $axios, query }) {
    try {
      let products = await $axios.$post(`${process.env.DEV_BACKEND}/api/search`, { title: query.title })
      return {
        products
      }
    } catch (error) {
      console.log(error);
    }
  }
}
